<template>
    <div>
        <div class="title">
            <span>{{ channelName }}</span>
        </div>
        <Loading v-if="isLoading" />
        <NewsList v-else :news="news"></NewsList>
    </div>
</template>

<script>
import NewsList from "@/components/news/NewsList"
import * as newsServices from "@/services/newsServices"
import Loading from "@/components/Loading"
export default {
    components: {
        NewsList,
        Loading,
    },

    data() {
        return {
            news: [],
            channelName: "",
            isLoading: true,
        }
    },

    created() {
    },

    methods: {
        async setChannelName() {
            var channels = await newsServices.getNewsChannels();
            var channel = channels.find(
                (item) => item.channelId == this.$route.params.id
            );
            this.channelName = channel.name;
        },

        async getNews() {
            this.isLoading = true;
            var resp = await newsServices.getNewsList(this.$route.params.id, 1, 10)
            this.news = resp.contentlist;
            this.isLoading = false;
        }
    },

    // 监控数据变化，当数据变化时执行一些事情
    watch: {
        "$route.params.id": {
            immediate: true, //立即监控参数中的id是否发生变化
            handler() {
                this.setChannelName(),
                this.getNews()
            }
        }
    },
}
</script>

<style scoped>
    .title {
        margin-bottom: 40px;
    }

    .title span {
        font-size: 28px;
        color: #00a3fe;
    }
</style>